/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.story h1 {
  width: auto;
  font-size: 2em;
  margin-right: auto;
  margin-bottom: 0.75em;
  display: inline-block;
  border-bottom: 3px solid #ff7979;
  padding-bottom: 0.125em; }

.story__inner {
  padding: 1.5em 2em; }

.story__content {
  max-width: 40em;
  margin: 0 auto 1.5em 0; }

.story__demo {
  background: #f5f5f5;
  border-radius: 0.25em;
  padding: 1.5em 1em; }
  .story__demo--rows {
    display: flex;
    flex-direction: row;
    flex-flow: row wrap; }
    .story__demo--rows .story__item {
      margin: 0 1em 1em;
      display: flex;
      flex-direction: column-reverse; }
      .story__demo--rows .story__item > *:not(.story__item-label) {
        margin: 0 1rem; }
      .story__demo--rows .story__item-label {
        font-weight: 600;
        color: #0fb9b1;
        display: block;
        text-align: center; }

.story__item-label {
  font-family: "Montserrat", sans-serif;
  font-size: 0.75em; }
