/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.button {
  color: #fff;
  font-family: "Noto Sans", sans-serif;
  background: #34495e;
  margin: 0;
  padding: 1em 1.25em;
  border: none;
  cursor: pointer;
  outline: none;
  transition: 0.25s ease-in-out;
  position: relative;
  overflow: hidden;
  border-radius: 0.25em; }
  .button__ripple {
    width: 1px;
    height: 1px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.2);
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    animation: ripple-effect 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955); }
  .button--primary {
    background: #34495e; }
  .button--secondary {
    background: #0fb9b1; }
  .button--tertiary {
    background: #ff7979; }

@keyframes ripple-effect {
  0% {
    opacity: 1; }
  100% {
    transform: translate(-50%, -50%) scale(500, 500);
    opacity: 0; } }
