/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.header {
  font-family: "Montserrat", sans-serif;
  width: 100%;
  margin: 0;
  padding: 0 0.5em;
  height: 4em;
  background: #34495e; }
  .header__inner {
    height: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center; }
  .header__logo {
    width: 1.75em;
    height: 1.75em;
    margin: 0 0.625em;
    z-index: 2;
    transition: filter 0.25s ease-in-out; }
  .header__nav {
    z-index: 1; }
  .header--full-width .header__inner {
    max-width: 100%; }
  .header--expanded .header__logo {
    filter: grayscale(100%); }
    .header--expanded .header__logo:hover {
      filter: grayscale(0); }

.header__hamburger {
  z-index: 2; }
  @media (min-width: 768px) {
    .header__hamburger {
      display: none; } }

@media (max-width: 767px) {
  .header--expanded .header__hamburger:hover .hamburger__bar {
    background-color: #ff7979; } }
