/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.chevron {
  width: 1em;
  height: 1em;
  display: block;
  position: relative; }
  .chevron--sm {
    font-size: 1em; }
  .chevron--md {
    font-size: 1.5em; }
  .chevron--lg {
    font-size: 2em; }
  .chevron--xl {
    font-size: 4em; }
  .chevron svg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto; }
  .chevron .chevron__line1, .chevron .chevron__line2 {
    stroke-width: 10;
    stroke-linecap: round;
    transition: transform 0.35s, stroke 0.35s; }
  .chevron__line1 {
    -webkit-transform-origin: 50px 50px;
    transform-origin: 50px 50px; }
  .chevron__line2 {
    -webkit-transform-origin: 50px 50px;
    transform-origin: 50px 50px; }
  .chevron--up, .chevron--down, .chevron--right, .chevron--left {
    display: block; }
    .chevron--up .chevron__container, .chevron--down .chevron__container, .chevron--right .chevron__container, .chevron--left .chevron__container {
      transition: transform 0.35s;
      transform: translateY(-13px);
      stroke: #333; }
    .chevron--up .chevron__line1, .chevron--down .chevron__line1, .chevron--right .chevron__line1, .chevron--left .chevron__line1 {
      transform: rotate(-40deg);
      stroke: #333; }
    .chevron--up .chevron__line2, .chevron--down .chevron__line2, .chevron--right .chevron__line2, .chevron--left .chevron__line2 {
      transform: rotate(40deg);
      stroke: #333; }
  .chevron--down .chevron__container, .chevron--right .chevron__container {
    transition: transform 0.35s;
    transform: translateY(13px); }
  .chevron--down .chevron__line1, .chevron--right .chevron__line1 {
    transform: rotate(40deg);
    stroke: #333; }
  .chevron--down .chevron__line2, .chevron--right .chevron__line2 {
    transform: rotate(-40deg);
    stroke: #333; }
  .chevron--left svg {
    transform: rotate(-90deg); }
  .chevron--left .chevron__line1 {
    stroke: #333; }
  .chevron--left .chevron__line2 {
    stroke: #333; }
  .chevron--right svg {
    transform: rotate(-90deg); }
  .chevron--right .chevron__line1 {
    stroke: #333; }
  .chevron--right .chevron__line2 {
    stroke: #333; }
